import React, { Component } from 'react';
import { Row, Col, Container } from 'react-bootstrap';
import { socials } from './config';

export class Footer extends Component {

    renderIcons(){
        const icons = [];
        for (let social in socials){
            icons.push(
                <Col key={social} as={'a'} className="py-1"  href={socials[social].link} target="_blank">
                    <img src={socials[social].src}
                         className='social-icon'
                         alt={'Connect with me on '+social}
                    /> 
                </Col>
            );
            icons.push(<Col key={social+'Buffer'}></Col>);
        }
        return (
            <Row noGutters className="social-container" xs={1} sm={6}>
                {icons}
            </Row>
        );
    }

    render(){
        return (
            <div className={'Footer'}>
                <Row noGutters>
                    <Col>
                        <h6 className="mb-2">Social</h6>
                        {this.renderIcons()}
                    </Col>
                    <Col id="footer-fun-links">
                        <h6 className="mb-2">Fun Links</h6>
                        <Row noGutters><Col as={'a'} href="http://www.vsauce.com/#/">Vsauce</Col></Row>
                        <Row noGutters><Col as={'a'} href="https://www.themoth.org/">The Moth</Col></Row>
                        <Row noGutters><Col as={'a'} href="https://xkcd.com/">xkcd</Col></Row>
                        <Row noGutters><Col as={'a'} href="http://www.weavesilk.com">Silk</Col></Row>
                    </Col>
                </Row>
            </div>
        );
    }
}
