import React, { Component } from 'react';
import { Container, Row, Col } from 'react-bootstrap';
import { Page, LayerGlitch } from '../Components';

export class AnimationPlayground extends Component {
    render() {
        return (
            <Page title={'Animation Playground'}>
                <Row noGutters>
                    <Col as={'p'}>
                        This page collects animations that I decide to play with.
                        Have fun exploring!
                    </Col>
                </Row>
                <Row noGutters xs={1} lg={2}>
                    <Col>
                        <h4 className="my-2">Glitchy Image</h4>
                        <LayerGlitch canReset />
                    </Col>
                </Row>
            </Page>
        );
    }
}
