import React, { Component } from 'react';
import { Row, Col } from 'react-bootstrap';
import { Page } from '../Components';


export class AboutMe extends Component {
    constructor(props) {
        super(props);
        this.state = {};
    }

    render(){
        return(
            <Page title={'About Me'}>
                <Row noGutters>
                    <Col as={'p'}>
                        If you wanted to learn more about the person that put this all together,
                        you're in the right place! Unfortunately, I'm still writing my biography.
                        Check back later and I might have something slightly more substantive than
                        this blurb apologizing for my inability to provide you with the content
                        that was so clearly promised by the title of this page.
                    </Col>
                </Row>
            </Page>
        );
    }
}