import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { Row, Col, Button } from 'react-bootstrap';
const layer1 = require('../../Images/graffiti-layer1.png');
const layer2 = require('../../Images/graffiti-layer2.png');
const layer3 = require('../../Images/graffiti-layer3.png');

export class LayerGlitch extends Component {
    constructor(props) {
        super(props);
        this.componentDidMount = this.componentDidMount.bind(this);
        this.resetLayers = this.resetLayers.bind(this);
    }

    componentDidMount(){
        document.querySelector('#glitch-img').addEventListener('mousemove', e => {
            const baseX = e.clientX - window.innerWidth/2+50;
            const baseY = e.clientY - window.innerHeight/2+50;
            document.querySelector('#layer2').style.transform = `translateX(${baseX/200+baseY/300+'%'}) translateY(${baseY/500*baseY/300+'%'})`;
            document.querySelector('#layer3').style.transform = `translateX(${baseY/200*baseX/300+'%'}) translateY(${baseX/300-baseY/200+'%'})`;
        });
    }

    resetLayers(){
        const l1 = document.querySelector('#layer2');
        const l2 = document.querySelector('#layer3');
        l1.style.transition = 'transform 0.2s';
        l2.style.transition = 'transform 0.2s';
        l1.style.transform = 'translate(0,0)';
        l2.style.transform = 'translate(0,0)';
        setTimeout(()=>{
            l1.style.transition = null;
            l2.style.transition = null;
        }, 200);
    }

    render(){
        return (
            <>
                <Row noGutters>
                    <Col>
                        <div id="glitch-img">
                            <div id='layer1' className='glitch-layer' style={{backgroundImage:`url(${layer1})`}} />
                            <div id='layer2' className='glitch-layer' style={{backgroundImage:`url(${layer2})`}} />
                            <div id='layer3' className='glitch-layer' style={{backgroundImage:`url(${layer3})`}} />
                        </div>
                    </Col>
                </Row>
                {
                    this.props.canReset && 
                    <>
                        <Row noGutters>
                            <Col>
                                <p>
                                    This effect is achieved by combining multiple png layers with plenty of transparent,
                                    negative space and manipulating them with a 'mousemove' event listener. Have fun
                                    moving your mouse around over this image and see what happens.
                                </p>
                            </Col>
                        </Row>
                        <Row noGutters>
                            <Col>
                                <Button variant="dark" onClick={this.resetLayers}>Reset</Button>
                            </Col>
                        </Row>
                    </>
                }
            </>
        );
    }
}

LayerGlitch.propTypes = {
    canReset: PropTypes.bool
};
