import React, { Component } from 'react';
import $ from 'jquery';
import { Navbar, Nav } from 'react-bootstrap';
import { Link } from 'react-router-dom';
import NavLink from './NavLink';
import logo from '../../Images/TE_logo-no-bg-sm.png';

const navLinks = (
    <>
        {NavLink({title:'Animations', path:'/AnimationPlayground'})}
        {NavLink({title:'About Me', path:'/AboutMe'})}
        {NavLink({title:'404'})}
    </>
);

export class NavBar extends Component {
    constructor(props) {
        super(props);
        this.state = {};

        // Out of Bounds Collapse Dropdown
        $('body').on('click.hideMenu', e => {
            // Interrupt infinite loop of clicks on .navbar-toggler
            if (e.target.classList[0] === 'navbar-toggler' ||
            e.target.classList[0] === 'navbar-toggler-icon') return;

            // else, trigger navbar-dropdown toggler click
            if($('#dropdown-nav').hasClass('show')){
                $('.navbar-toggler').click();
            }
        });
    }

    render(){
        return (
            <div>
                <Navbar className="real" variant="dark" bg="dark" expand="md">
                    <Link to='/'>
                        <Navbar.Brand>
                            <img 
                                src={logo} 
                                alt="Tyler Ekizian's Site Logo" 
                                width="40" 
                                height="40" 
                                className="d-inline-block alight-top"
                                />
                            </Navbar.Brand>
                        </Link>
                    <Navbar.Toggle aria-controls="dropdown-nav"/>
                    <Navbar.Collapse id="dropdown-nav">
                        <Nav>
                            {navLinks}
                        </Nav>
                    </Navbar.Collapse>
                </Navbar>
                <Navbar className="dummy" variant="dark" bg="dark" expand="md">
                    <Navbar.Brand>
                        <img 
                            src={logo} 
                            alt="Tyler Ekizian's Site Logo" 
                            width="40" 
                            height="40" 
                            className="d-inline-block alight-top"
                            />
                    </Navbar.Brand>
                </Navbar>
            </div>
        );
    }
}
