import React, { Component } from 'react';
import { Page } from '../Components';
import logo from '../Images/TE_logo-no-bg-sm.png';

export class Home extends Component {
    render(){
        return(
            <Page title={'Welcome'}>
                <p>
                    Currently <code>under construction</code>.
                </p>
                <br />
                <p>
                    This site serves as a playground for building skills. Hopefully, it will
                    update and change frequently as I get further inspiration (and time to
                    work on things).
                </p>
                <br />
                <img src={logo} className="App-logo" alt="logo" />
                <br/>
            </Page>
        );
    }
}