import React, { Component } from 'react';
import { Page } from '../Components';
import logo from '../Images/TE_logo-no-bg-sm.png';

export class Error404 extends Component {
    constructor(props) {
        super(props);
        this.state = {};
    }

    render(){
        const intentional = window.location.pathname === "/404";
        return(
            <Page title={ intentional ? "Why?" : "You Must Be Lost"}>
                <img src={logo} className="App-logo" alt="logo" />
                <br/>
                <p>
                Currently <code>under construction</code>.
                </p><br />
                {
                    intentional ?
                    <p>
                        Congrats, you've received a 200 response for the 404 page. <br/>
                        Quite the paradox, but it's happened. <br /><br />
                        What now...?
                    </p>
                    :
                    <>
                        <p>
                            Maybe this path will be available later.
                        </p>
                        <p>
                            Until then, please stop poking around.
                        </p>
                    </>
                }
            </Page>
        );
    }
}